import * as React from 'react';
import * as ReactDOM from 'react-dom';

import KendoBaseComponent, { KendoWidgetProps } from '@progress/kendo-base-component-react-wrapper';

export interface AutoCompleteProps extends KendoWidgetProps, kendo.ui.AutoCompleteOptions {
    children?: React.ReactNode;
}

export default class AutoComplete extends KendoBaseComponent<AutoCompleteProps, {}> {
    componentDidMount(): void {
        const domNode: Element = ReactDOM.findDOMNode(this as React.ReactInstance);
        const { children, widgetRef, ...props } = this.props;

        this.widgetInstance = new kendo.ui.AutoComplete(domNode, (props as kendo.ui.AutoCompleteOptions));

        super.componentDidMount();
    }

    componentWillReceiveProps(nextProps: AutoCompleteProps) {
        const { children, widgetRef, dataSource, ...props } = nextProps;

        this.widgetInstance.setOptions(props);
        const widget = (this.widgetInstance as kendo.ui.AutoComplete);

        if (widget.options.dataSource !== dataSource) {
            widget.setDataSource(dataSource);
        }
        widget.value(nextProps.value as string);
    }

    render(): any {
        return (this.props.children || <input />);
    }
}


