import * as React from 'react';
import * as ReactDOM from 'react-dom';

import KendoBaseComponent, { KendoWidgetProps } from '@progress/kendo-base-component-react-wrapper';

export interface MultiSelectProps extends KendoWidgetProps, kendo.ui.MultiSelectOptions {
    children?: React.ReactNode;
}

export default class MultiSelect extends KendoBaseComponent<MultiSelectProps, {}> {
    componentDidMount(): void {
        const domNode: Element = ReactDOM.findDOMNode(this as React.ReactInstance);
        const { children, widgetRef, ...props } = this.props;

        this.widgetInstance = new kendo.ui.MultiSelect(domNode, (props as kendo.ui.MultiSelectOptions));

        super.componentDidMount();
    }

    componentWillReceiveProps(nextProps: MultiSelectProps) {
        const { children, widgetRef, dataSource, ...props } = nextProps;

        this.widgetInstance.setOptions(props);
        const widget = (this.widgetInstance as kendo.ui.MultiSelect);

        if (widget.options.dataSource !== dataSource) {
            widget.setDataSource(dataSource);
        }
        widget.value(nextProps.value);
    }

    render(): any {
        return (this.props.children || <select />);
    }
}


