import * as React from 'react';
import * as ReactDOM from 'react-dom';

import KendoBaseComponent, { KendoWidgetProps } from '@progress/kendo-base-component-react-wrapper';

export interface DropDownListProps extends KendoWidgetProps,  kendo.ui.DropDownListOptions {
    children?: React.ReactNode;
}

export default class DropDownList extends KendoBaseComponent<DropDownListProps, {}> {
    componentDidMount(): void {
        const domNode: Element = ReactDOM.findDOMNode(this as React.ReactInstance);
        const { children, widgetRef, ...props } = this.props;

        this.widgetInstance = new kendo.ui.DropDownList(domNode, props);

        super.componentDidMount();
    }

    componentWillReceiveProps(nextProps: DropDownListProps): void {
        const { children, widgetRef, dataSource, ...props } = nextProps;
        this.widgetInstance.setOptions(props);
        const widget = (this.widgetInstance as kendo.ui.DropDownList);

        if (widget.options.dataSource !== dataSource) {
            widget.setDataSource(dataSource);
        }
        (this.widgetInstance as kendo.ui.DropDownList).value(nextProps.value as string);
    }

    render(): any {
        return (this.props.children || <input />);
    }
}


